import GitHubCalendar from "react-github-calendar";
import ReactTooltip from "react-tooltip";


const GithubStats=()=>{
    return <section className="bg-secondary py-5">
        <div className="container mx-auto
         text-center bg-secondary w-full 
          text-white py-5">
            <p className="text-xl">GitHub Stats</p>
        </div>
        <div className="container mx-auto
         text-center bg-tertiary w-full 
          text-white">
            <GitHubCalendar
            style={{
                textAlign:'center',
                // border:'1px solid red',
                padding:15,
                margin:'auto'
            }}
            hideColorLegend={true}
            // color="rgba(153, 94, 15, 1)"
            username="tejasbishnoi21"  // TejasBishnoi21 Ravinder1310=> ravinder1310
            >
                <ReactTooltip delayShow={20} html/>
            </GitHubCalendar>
        </div>
    </section>
}
export default GithubStats;