import { contact } from "../data";

const Contact=()=>{
    function Mailto({ email, subject, body, ...props }) {
        return (
          <a 
          href={`mailto:${email}?subject=${subject || ""}&body=${body || ""}`}>
            {props.children}
          </a>
        );
      }

    return <section id="contact"
    className="section bg-primary">
        <div className="container mx-auto">
            {/*  Title Div */}
            <div className="flex flex-col
            items-center text-center">
                <h2 
                className="section-title
                before:content-contact relative
                before:absolute before:opac-40
                before:-top-7
                before:-left-40
                before:hidden
                before:lg:block">
                    Contact Me
                </h2>
                <p className="subtitle text-accent">
                I'm actively looking for full-time opportunities in web development and am open to work. 
                If you find my profile interesting, please feel free to reach out.
                </p>
            </div>

            <div className="flex flex-col lg:gap-x-8
            lg:flex-row">
                {/* Info Div */}
                <div className="flex flex-1 flex-col
                items-start space-y-8 mb-12 lg:mb-0 lg:pt-2">
                    {
                        contact.map((el, index)=>{
                            const {icon, title, subtitle, description, remote, phone} = el;

                            return <div className="flex flex-col lg:flex-row
                            gap-x-4">
                                <div className="text-accent
                                rounded-sm w-14 h-14 flex items-start
                                justify-center mt-2 mb-4 lg:mb-0 text-2xl">
                                    {icon}
                                </div>
                                <div>
                                    <h4 className="font-body text-xl mb-1">
                                        {title}
                                    </h4>
                                    <p className="mb-1">{subtitle}</p>
                                    
                                    <div className="text-accent font-normal 
                                    hover:underline cursor-pointer text-m">
                                        <Mailto 
                                        email="tejas.vishnoi@gmail.com" 
                                        subject="" 
                                        body="Hello Tejas!">
                                        {description}
                                        </Mailto>
                                    </div>
                                    <p className="text-accent font-normal 
                                      text-sm">
                                        {remote}
                                    </p>
                                    <p className="text-accent font-normal 
                                      text-sm">
                                        {phone}
                                    </p>
                                </div>
                            </div>
                        })
                    }
                </div>

                {/* Contact Form Div */}
                <div>
                    <form className="space-y-8
                    w-full max-w-[780px]">
                        <div className="flex gap-8">
                            <input className="input" type="text" placeholder="Enter your name"/>
                            <input className="input" type="email" placeholder="Enter your email"/>
                        </div>

                        <input type="text" className="input" placeholder="Subject"/>
                        <textarea 
                        className="textarea"
                        placeholder="Your message"
                        ></textarea>

                        <button className="btn btn-lg bg-accent
                        hover:bg-accent-hover">
                            Send Message
                        </button>
                    </form>
                </div>
            </div>
        </div>
    </section>
}

export default Contact;